:root {
  --black: #000000;
  --french-gray: #bdc2c9;
  --fuscous-gray: #525252;
  --gray: #808080;
  --log-cabin: #222222;
  --mystic: #e0e6ef;
  --sapphire: #2d53a5;
  --shark: #1d252c;
  --shuttle-gray: #61676b;
  --white: #ffffff;
  --zircon: #f6f8ff;

  --font-size-10_5px: 10.5px;
  --font-size-12_1px: 12.1px;
  --font-size-5_4px: 5.4px;
  --font-size-5_8px: 5.8px;
  --font-size-6_5px: 6.5px;
  --font-size-l: 20px;
  --font-size-m: 19px;
  --font-size-s: 18px;
  --font-size-xl: 24px;
  --font-size-xs: 17px;
  --font-size-xxl: 36px;
  --font-size-xxs: 16px;
  --font-size-xxxs: 15px;
  --font-size-xxxxs: 14px;
  --font-size-xxxxxs: 12px;

  --font-family-inter: "Inter", Helvetica;
  --font-family-proxima_nova-regular: "Proxima Nova-Regular", Helvetica;
  --font-family-roboto: "Roboto", Helvetica;
}
.inter-normal-log-cabin-19px {
  color: var(--log-cabin);
  font-family: var(--font-family-inter);
  font-size: var(--font-size-m);
  font-style: normal;
  font-weight: 400;
}
.inter-normal-gray-16px {
  color: var(--gray);
  font-family: var(--font-family-inter);
  font-size: var(--font-size-xxs);
  font-style: italic;
  font-weight: 400;
}
.inter-normal-log-cabin-17px {
  color: var(--log-cabin);
  font-family: var(--font-family-inter);
  font-size: var(--font-size-xs);
  font-style: normal;
  font-weight: 400;
}
.proximanova-regular-normal-shark-5-8px {
  color: var(--shark);
  font-family: var(--font-family-proxima_nova-regular);
  font-size: var(--font-size-5_8px);
  font-style: normal;
  font-weight: 400;
}
.inter-bold-log-cabin-36px {
  color: var(--log-cabin);
  font-family: var(--font-family-inter);
  font-size: var(--font-size-xxl);
  font-style: normal;
  font-weight: 700;
}
.inter-normal-black-12px {
  color: var(--black);
  font-family: var(--font-family-inter);
  font-size: var(--font-size-xxxxxs);
  font-style: normal;
  font-weight: 400;
}
.inter-normal-black-14px {
  color: var(--black);
  font-family: var(--font-family-inter);
  font-size: var(--font-size-xxxxs);
  font-style: normal;
  font-weight: 400;
}
.inter-semi-bold-log-cabin-20px {
  color: var(--log-cabin);
  font-family: var(--font-family-inter);
  font-size: var(--font-size-l);
  font-style: normal;
  font-weight: 600;
}
.inter-normal-gray-14px {
  color: var(--gray);
  font-family: var(--font-family-inter);
  font-size: var(--font-size-xxxxs);
  font-style: italic;
  font-weight: 400;
}
